import { useState,useEffect } from 'react';
import { useParams } from 'react-router-dom';
import Movie from '../components/Movie';
import styles from './Detail.module.css';
import { Link } from 'react-router-dom';

function Detail() {
    const [movie, setMovie] = useState([]);
    const [loading, setLoading] = useState(true);
    const {id} = useParams();

    const getMovie = async () => {
        const json = await (
            await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)).json();
            console.log(json.data.movie);
            setMovie(json.data.movie);
            setLoading(false);
    };
    useEffect(() => {
        getMovie();
    }, []);
    return (
    <div className={styles.container}>
    <div className={styles.loader}>
        {loading ? <h1>Now Loading..</h1> : 
        <div>
            <hr/>
            <img className={styles.imgRadius, styles.movie__img} src={movie.medium_cover_image}/>
            <h1 className={styles.sexyFont} style={{ color:"tomato" }}>{movie.title}</h1>
            <h3 className={styles.sexyFont}>Years : {movie.year}</h3>
            <h5 className={styles.sexyFont}>Some People's Like : {movie.like_count} ðŸ’œ </h5>
            <h5 className={styles.sexyFont}>Total download count : {movie.download_count} !!</h5>
            <h4 className={styles.sexyFont}>This Movie is : {movie.description_full}</h4>
            <hr/>
            <div>
                <h5 className={styles.sexyFont}>
                Download Torrent :
                    <ul>

                    </ul>
                    
                    
                </h5>
                <h5 className={styles.sexyFont}>
                    <Link to={'/'}>âœ¿Back to Home</Link>
                </h5>
            </div>
        </div>
        }
        
    </div>
    </div>
    )
}
export default Detail;