import propTypes from 'prop-types';
import { Link } from 'react-router-dom';
import styles from "./Movie.module.css";

function Movie({ id, title, coverImg, year, summary, genres }) {
return(
        <div className={styles.movie}>
          <img className={styles.movie__img} src={coverImg} alt={title}/>
          <h2 className={styles.sexyFont, styles.movie__title} style={{color:"tomato"}}>
            <Link to={`/movie/${id}`}>{title}</Link>
            </h2>
            <h3 className={styles.movie__title}>{year}</h3>
            <p className={styles.sexyFont}>{summary.length > 235 ? `${summary.slice(0, 235)}...` : summary}</p>
          <ul className={styles.movie__genres, styles.sexyFont}>
            {genres.map((g) => 
            (<li key={g}>{g}</li>
              ))}
          </ul>
          <hr/>
        </div>
)
};

Movie.propTypes ={
    id : propTypes.number.isRequired,
    coverImg : propTypes.string.isRequired,
    year : propTypes.number.isRequired,
    title : propTypes.string.isRequired,
    summary : propTypes.string.isRequired,
    genres : propTypes.arrayOf(propTypes.string).isRequired,
}

export default Movie;